<script>
import $ from 'jquery';
import Footer from './Footer';
import HomeNavbar from './HomeNavbar'

export default {
    components:{
       Footer,
       HomeNavbar,
    },

    data(){
        return{
            products: [],
            isLoading: false,
            product: {},
            status:{
                loadingItem: '',
            },
        }
    },
    
    methods: {
       
        to(type){
            const vm = this;
            vm.$router.push(`/set/shopping?type=${type}`); 
        },
        getProducts(page = 1){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
            const vm = this;

                console.log(process.env.APIPATH, process.env.CUSTOMPATH)
                vm.isLoading = true;
                this.$http.get(api).then((response) => {
                    console.log(response.data);
                    vm.isLoading = false;
                      vm.products = response.data.products;
                    vm.pagination = response.data.pagination;
                });
        },
        getProduct(id){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`
            vm.status.loadingItem = id;
            this.$http.get(url).then((response) => {
                vm.product = response.data.product;
                // $('#productModal').modal('show');
                console.log(response);
                vm.status.loadingItem = '';
                vm.$router.push(`set/shopping/${response.data.product.id}`);
            })
        },
        scroll() {
            // 
            const scrollPos = $(window).scrollTop();
            const windowHeight = $(window).height();
            $('.animated').each(function animated(){
                var thisPos = $(this).offset().top;
                if((windowHeight + scrollPos)>= thisPos+100){
                    $(this).addClass('fadeIn')
                }
            });
            $('.animated-left').each(function animatedLeft(){
                var thisPos = $(this).offset().top;
                if((windowHeight + scrollPos)>= thisPos+100){
                    $(this).addClass('fadeInX')
                }
            });
            $('.animated-right').each(function animatedRight(){
                var thisPos = $(this).offset().top;
                if((windowHeight + scrollPos)>= thisPos+100){
                    $(this).addClass('fadeInX')
                }
            });
        },
        textHidden(){
            $(function(){
            var len = 55; // 55"..."
            $(".ellipsis").each(function(i){
                if($(this).text().length>len){
                    $(this).attr("title",$(this).text());
                    var text=$(this).text().substring(0,len-1)+"...";
                    $(this).text(text);
                }
            });
        });
        }
    },
    created(){
        this.getProducts();
        this.textHidden();
        window.addEventListener('scroll', this.scroll);
    },
    // destroyed() {
    //     window.removeEventListener('scroll', this.scroll);
    // },
    computed: {
        filterProducts: function(){
             var hotProducts = [];
             
            this.products.forEach(function(item){
          if(item.content == ''){
            hotProducts.push(item);
          }
        })
        return hotProducts;
        }
    }
}
</script>

